import React, { useEffect, useRef, useState } from "react";
import { Button, ButtonGroup, FormControl, InputGroup } from "react-bootstrap";
import axios from "axios";
import "../assets/css/anime.css";

const ANIME_URL = process.env.REACT_APP_URL_ANIME ? process.env.REACT_APP_URL_ANIME : "https://gogoanime.sk/";
const CORS_ANYWHERE = process.env.REACT_APP_URL_CORSANYWHERE ? process.env.REACT_APP_URL_CORSANYWHERE : "";

const Anime = () => {
    const [url, setUrl] = useState<string>("");
    const [name, setName] = useState<string>("");
    const [maxEpisode, setMaxEpisode] = useState<number>(1);
    const [currentEpisode, setCurrentEpisode] = useState<number>(1);
    const [foundedList, setFoundedList] = useState<{ releaseDate: string, name: string, img: string, href: string }[]>([]);

    const ref_foundStatus: React.MutableRefObject<any> = useRef(null);
    const ref_btnStat: React.MutableRefObject<any> = useRef(null);
    const ref_ifContainer: React.MutableRefObject<any> = useRef(null);

    const ref_found: React.MutableRefObject<any> = useRef(null);
    const ref_resolution: React.MutableRefObject<any> = useRef(null);
    const ref_findTxt: React.MutableRefObject<any> = useRef(null);
    const ref_animePic: React.MutableRefObject<any> = useRef(null);

    useEffect(() => {
        setUrl(localStorage.getItem("storage_url") || "");
        setName(localStorage.getItem("selectedAnime") || "");
        setCurrentEpisode(localStorage.getItem("currentEp") ? +localStorage.getItem("currentEp")! : 1);
        setMaxEpisode(localStorage.getItem("storage_maxEpisode") ? +localStorage.getItem("storage_maxEpisode")! : 1);

        return () => { };
    }, []);

    const handleInputChange = (e: any) => {
        setCurrentEpisode(e.target.value);
    };

    //Not Working
    function getAnimeOfTheWeekList() {
        axios
            .get(`${CORS_ANYWHERE}https://anitrendz.net`)
            .then((html) => {
                const parser = new DOMParser();
                const doc = parser.parseFromString(html.data, "text/html");
                const sel: any = doc.querySelector(".at-m-top-anime")!.firstElementChild!.firstElementChild!.getAttribute('src');
                if (sel)
                    document.location.href = sel;
            })
            .catch(() => {
                statusMessageChanger(ref_foundStatus, "Connection/Proxy Issue", "red")
            });
    }

    function statusMessageChanger(ref, text, color?) {
        ref.current.innerText = text;
        color ? (ref.current.style.color = color) : ref.current.style.color = "white";
    }

    //Loading the selected anime in foundList to the downloader/viewer
    function LoadFound() {
        if (foundedList[ref_found.current.value]?.href) {
            let choice = ref_found.current.value;
            let unmodhref = foundedList[choice].href;
            let href = unmodhref.slice(unmodhref.indexOf("category"));
            setCurrentEpisode(1);

            ////selectedAnime
            setName(foundedList[choice].name);

            statusMessageChanger(ref_foundStatus, "Done", "white")

            let mhref = `${CORS_ANYWHERE}+${ANIME_URL}`;
            mhref = mhref.concat(href);
            setUrl(mhref);
            mhref = mhref.concat(`-episode-1`);
            if (mhref.indexOf("/category/") !== -1) {
                mhref = mhref.replace("/category/", "/");
                setUrl(mhref);
            }

            statusMessageChanger(ref_foundStatus, "Loaded", "green")

            autoEpisodeUpdate(mhref);
        } else {
            statusMessageChanger(ref_foundStatus, "Bad Selection", "red")
        }
    }

    function autoEpisodeUpdate(Url: string) {
        axios.get(Url).then((html) => {
            const parser = new DOMParser();
            const doc = parser.parseFromString(html.data, "text/html");
            const getend = doc.querySelector("#episode_page")!.lastElementChild!.children[0].getAttribute("ep_end")!;
            setMaxEpisode(+getend);

            localStorage.setItem("storage_maxEpisode", getend);

            const del = Url.indexOf("-episode-1");
            Url = Url.slice(0, del);
            setUrl(Url);
        });
    }

    function changePic() {
        if (ref_found.current.value !== '') {
            let choice = ref_found.current.value;
            ref_animePic.current.src = foundedList[choice].img;
        }
    }

    function Find() {
        statusMessageChanger(ref_foundStatus, "Searching Please Wait", "white")

        let findTxt = ref_findTxt.current.value;
        try {
            findTxt = findTxt.replace(" ", "%20");
        } catch (error) {
            console.log(error);
        }
        let UrlF = `${CORS_ANYWHERE}${ANIME_URL}search.html?keyword=`;
        UrlF = UrlF.concat(`${findTxt}`);
        //Get response from URL
        pingFindList(UrlF);
    }

    function pingFindList(UrlF: string) {
        axios
            .get(UrlF)
            .then(async (html) => {
                const parser = new DOMParser();
                const doc = parser.parseFromString(html.data, "text/html");
                //handle pagination
                const pg = doc.querySelector(".pagination-list")!.children;

                let animeList: { releaseDate: string, name: string, img: string, href: string }[] | [] = []

                if (pg) {
                    for (let index = 0; index < pg.length; index++) {
                        const temp = UrlF.concat(`&page=${index + 1}`);

                        animeList = [...animeList, ...await addToFindList(temp)]
                    }
                } else {
                    animeList = [...animeList, ...await addToFindList(UrlF)]
                }

                setFoundedList(animeList)
            })
            .catch(() => {
                statusMessageChanger(ref_foundStatus, "Connection/Name Issue", "red")
            });
    }

    //adds the items from the founded page
    async function addToFindList(UrlF: string) {
        const finalArr: { releaseDate: string, name: string, img: string, href: string }[] = []

        await axios.get(UrlF).then((html) => {
            const parser = new DOMParser();
            const fetchedHTML = parser.parseFromString(html.data, "text/html");

            const HTMLitems = fetchedHTML.querySelector(".items")!.children;
            const itemsArray: any = Array.from(HTMLitems);

            // Add -options- into the dropdown list from an array of html items (contains child nodes)
            itemsArray.forEach(function (item: any, i: number) {
                finalArr.push({
                    releaseDate: item.children[2].innerText.replace("Released: ", "").match(/[0-9]+/g).join(''),
                    name: item.children[1].children[0].innerText,
                    img: item.children[0].firstElementChild.firstElementChild.src,
                    href: (item.children[1].children[0].href)
                })
            });

            statusMessageChanger(ref_foundStatus, "Found", "green")

            //Modify img
            ref_animePic.current.src = itemsArray[0].children[0].firstElementChild.firstElementChild.src;


        }).catch(() => {
            statusMessageChanger(ref_foundStatus, "Connection/Name Issue", "red")
        });

        return finalArr;
    }

    function localStorageSave(Url: string) {
        localStorage.setItem("storage_url", Url);
        localStorage.setItem("currentEp", currentEpisode.toString());
        localStorage.setItem("selectedAnime", name);
    }

    function AddIframe() {
        let htmlC = `<iframe class='iframe' scrolling="no" src="" style="border: 0px none;  height: 920px; margin-top: -450px; width: 100%;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`;
        ref_ifContainer.current.innerHTML = htmlC;
    }

    function Watch() {
        AddIframe();
        let Url = url;
        let goUrl = parseURL(Url);
        ref_ifContainer.current.children[0].src = goUrl;
    }

    function GoDownload() {

        statusMessageChanger(ref_btnStat, "Parsing URL...", "white")

        //AddIframe();
        let Url = url;
        let parsedUrl = parseURL(Url);
        sendParsedRequest(parsedUrl);
    }

    function parseURL(Url: string) {
        if (Url.indexOf("/category/") !== -1) {
            Url = Url.replace("/category/", "/");
        }
        if (Url.indexOf("-episode") !== -1) {
            let del = Url.indexOf("-episode");
            Url = Url.slice(0, del);
            setUrl(Url);
            localStorageSave(Url);

            return Url.concat(`-episode-${currentEpisode}`);
        } else {
            localStorageSave(Url);
            return Url.concat(`-episode-${currentEpisode}`);
        }
    }

    function sendParsedRequest(link: string) {
        statusMessageChanger(ref_btnStat, "Waiting For Response...", "white")
        let err: any = document.querySelector("#webs");
        axios
            //.get(`https://corsgogo.herokuapp.com/${link}`)
            .get(`${link}`)
            .then((html) => {
                if (err)
                    err.style.color = "white";
                const parser = new DOMParser();
                const doc = parser.parseFromString(html.data, "text/html");
                const URL: any = doc.querySelector(".dowloads")!.children[0]!.getAttribute('href');

                statusMessageChanger(ref_btnStat, "Response Granted", "green")
                window.open(URL, '_blank');
                //processResponse(URL);
            })
            .catch(() => {
                statusMessageChanger(ref_btnStat, "Url Or Connection Error Retry", "red")
            });
    }
    return (
        <div className="anime-container">
            <header id="anime-section-title">
                <h1>BTanime</h1>
                <h2>Download Or Watch English Anime Online</h2>
            </header>
            <main className="anime-card">
                <Button onClick={getAnimeOfTheWeekList} variant="outline-light" id="GTA">
                    Anime Of The Week List
                </Button>
                <div id="anime-container-inner">
                    <section id="anime-section-find">
                        <div>
                            <h2 style={{ fontFamily: "Prototype" }}>Anime Finder</h2>
                            <InputGroup>
                                <FormControl
                                    ref={ref_findTxt}
                                    id="findTxt"
                                    placeholder="Anime Name"
                                    aria-label="Anime Name"
                                    aria-describedby="basic-addon2"
                                />
                                <Button onClick={Find} id="FBtn" variant="outline-light">
                                    Find
                                </Button>
                            </InputGroup>

                            <select
                                className="form-control"
                                id="found"
                                onChange={changePic}
                                ref={ref_found}
                            >
                                {foundedList?.map((item, index) => { return <option key={item.name} value={index}>{item.name} {item.releaseDate}</option> })}
                            </select>

                            <Button variant="dark" onClick={LoadFound} id="LoadBtn">
                                Load Into Downloader
                            </Button>
                            <div id="foundlistlbl" ref={ref_foundStatus}>

                            </div>
                        </div>
                        <img
                            ref={ref_animePic}
                            id="animePic"
                            src={require("../assets/img/anime.webp")}
                            width="200px"
                            height="282px"
                            alt="Anime pic"
                        />
                    </section>
                    <section id="anime-section-download">
                        <div>
                            <h2 style={{ fontFamily: "Prototype" }}>Anime Downloader</h2>
                            <InputGroup>
                                <FormControl
                                    disabled
                                    id="webs"
                                    placeholder="Anime Url"
                                    aria-label="Anime Url"
                                    aria-describedby="basic-addon2"
                                    value={url}
                                />
                            </InputGroup>
                            <InputGroup>
                                <FormControl
                                    disabled
                                    id="selectedAnime"
                                    placeholder="Selected Anime"
                                    aria-label="Selected Anime"
                                    aria-describedby="basic-addon2"
                                    value={name}
                                />
                            </InputGroup>

                            <label className="mt-3" htmlFor="ep">
                                Episode Number:
                            </label>
                            <input
                                className="form-control"
                                type="number"
                                id="ep"
                                name="ep"
                                min="1"
                                max={maxEpisode}
                                value={currentEpisode}
                                onChange={handleInputChange}
                            />
                            <div style={{ textAlign: "right", paddingRight: "1rem" }}>
                                Max Episodes: <span id="mx-ep">{maxEpisode}</span>
                            </div>
                            <label hidden={true} className="mt-2" htmlFor="resolution">
                                Resolution:
                            </label>
                            <select hidden={true}
                                className="form-control"
                                id="resolution"
                                ref={ref_resolution}
                            >
                                <option value="2">External Link</option>
                                {/* <option value="1">First Internal</option>
                                <option value="3">Second Internal</option>
                                <option value="4">Third Internal</option>
                                <option value="5">Extra(LOW) Internal</option>*/}
                            </select>

                            <div className="mt-2">
                                <ButtonGroup
                                    aria-label="Basic example"
                                    style={{ display: "flex" }}
                                >
                                    <Button
                                        onClick={GoDownload}
                                        variant="dark"
                                        type="submit"
                                        id="Btn"
                                    >
                                        Download
                                    </Button>
                                    <Button
                                        onClick={Watch}
                                        variant="dark"
                                        type="submit"
                                        id="watchbtn"
                                    >
                                        Watch
                                    </Button>
                                </ButtonGroup>
                            </div>
                            <div id="btnStat" ref={ref_btnStat}>
                            </div>
                        </div>
                    </section>
                </div>
                <div
                    id="ifContainer"
                    ref={ref_ifContainer}
                    style={{
                        border: "0px solid #D5CC5A",
                        overflow: "hidden",
                        margin: "15px auto",
                        maxWidth: "612px",
                    }}
                ></div>
            </main>
        </div>
    );
};

export default Anime;
