import React, { useEffect, useState } from "react";
import { Navbar, Nav } from "react-bootstrap";
import Logo from "../assets/img/Logo.png";
import Logolgt from "../assets/img/Logo-lgt.png";
import { debounce } from "../utils/General";
import "../assets/css/nav.css";


interface Props {
  RefList?: any;
  executeScroll?: (e: any) => void;
  logoText: string;
  navBackground: any;
  hasLinks: boolean;
}

//Note: Try Intersection observer instead
//Todo: Make it a generic component (props array)
function windowScroll(stateChanger: React.Dispatch<React.SetStateAction<number>>) {
  let result = 0;
  let modHeight = [600, 1120, 1800, 2400];
  if (window.innerWidth < 480) {
    modHeight[2] = 2200;
    modHeight[3] = 2800;
  } else {
    modHeight[3] = 2400;
  }
  if (window.scrollY > 0 && window.scrollY < modHeight[0]) {
    result = 1; //about
  } else if (
    window.scrollY >= modHeight[0] &&
    window.scrollY < modHeight[1]
  ) {
    result = 2; //skills
  } else if (
    window.scrollY >= modHeight[1] &&
    window.scrollY < modHeight[2]
  ) {
    result = 3; //technologies
  } else if (
    window.scrollY >= modHeight[2] &&
    window.scrollY < modHeight[3]
  ) {
    result = 4; //projects
  } else if (window.scrollY >= modHeight[3]) {
    result = 5; //contacts
  } else {
    result = 1;
  }
  stateChanger(result)
};

export const NavFull = (props: Props) => {
  const [UnderScore, setUnderScore] = useState(1);

  const handleSelect = (e: any) => {
    if (props.executeScroll)
      props.executeScroll(e);
  };

  //this doesn't look right also useLayoutEffects?
  useEffect(() => {
    if (props.hasLinks === true) {
      window.scrollTo(0, 0);
      window.addEventListener("scroll", debounce(windowScroll, 100, setUnderScore));
    }
    else {
      window.removeEventListener("scroll", debounce(windowScroll, 100, setUnderScore));
    }
    return () => {
      if (props.hasLinks === true) {
        window.removeEventListener("scroll", debounce(windowScroll, 100, setUnderScore));
        setUnderScore(0);
      }
    };
  }, [props.hasLinks]);

  return (
    <div >
      <Navbar className={props.navBackground}
        onSelect={handleSelect}
        expand="sm"
        bg={props.navBackground}
        variant={props.navBackground}
        fixed="top"
        style={{ width: props.hasLinks ? "auto" : "fit-content" }}
      >
        <Navbar.Brand href="/">
          <img
            alt=""
            src={props.logoText ? Logolgt : Logo}
            width="36"
            height="30"
            className="d-inline-block align-top"
          />
          <span className="logoText">{" " + props.logoText}</span>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        {props.hasLinks && <Navbar.Collapse
          id="responsive-navbar-nav"
          className="justify-content-end"
        >
          <Nav.Link eventKey="1">
            About
            <div
              style={{ width: 50 }}
              className={
                "UnderScoreLink" + (UnderScore === 1 ? "Active" : "")
              }
            />
          </Nav.Link>
          <Nav.Link eventKey="2">
            Services
            <div
              style={{ width: 50 }}
              className={
                "UnderScoreLink" + (UnderScore === 2 ? "Active" : "")
              }
            />
          </Nav.Link>
          <Nav.Link eventKey="3">
            Technologies
            <div
              style={{ width: 115 }}
              className={
                "UnderScoreLink" + (UnderScore === 3 ? "Active" : "")
              }
            />
          </Nav.Link>
          <Nav.Link eventKey="4">
            Projects
            <div
              style={{ width: 70 }}
              className={
                "UnderScoreLink" + (UnderScore === 4 ? "Active" : "")
              }
            />
          </Nav.Link>
          <Nav.Link eventKey="5">
            Contact
            <div
              style={{ width: 70 }}
              className={
                "UnderScoreLink" + (UnderScore === 5 ? "Active" : "")
              }
            />
          </Nav.Link>
        </Navbar.Collapse>
        }
      </Navbar>
    </div>
  );
};
