import React from "react";
import { Container, Figure } from "react-bootstrap";
import { InfiniteCarousel } from "../ImportExport";
import "../assets/css/skills.css";

//import all static Images
function importAll(r: __WebpackModuleApi.RequireContext) {
  return r.keys().map(r);
}
const TechnologyLinks = importAll(require.context('../assets/img/brands/', false, /\.(png|jpe?g|svg|webp)$/));

const Skills = () => {

  return (
    <div>
      <div className="skills-container">
        <Container>
          <h2 className="skills-title">Services</h2>
        </Container>

        <Container className="skills-container-text text-left ">
          <div className="skills-body">
            <ul>
              <li>Full-Stack Web Development</li>
              <li>System Analysis And Design</li>
              <li>Logo, Ads, Thumbnail Design</li>
              <li>Website Design</li>
              <li>Photo Editing</li>
              <li>3D Modeling Texturing and Animation</li>
              <li>Video Post-Production</li>
            </ul>
          </div>
        </Container>

        <Container fluid className="skills-carousel ">
          <InfiniteCarousel
            breakpoints={[
              {
                breakpoint: 500,
                settings: {
                  slidesToShow: 4,
                  slidesToScroll: 1,
                },
              },
              {
                breakpoint: 768,
                settings: {
                  slidesToShow: 5,
                  slidesToScroll: 1,
                },
              },
              {
                breakpoint: 960,
                settings: {
                  slidesToShow: 10,
                  slidesToScroll: 1,
                },
              },
            ]}
            dots={false}
            showSides={false}
            sidesOpacity={1}
            sideSize={0.1}
            slidesToScroll={8}
            slidesToShow={10}
            slidesSpacing={3}
            dragging={true}
            scrollOnDevice={false}
            autoCycle={false}
            cycleInterval={6000}
          >
            {TechnologyLinks.map((item: any, i: number) => (
              <Figure key={i}>
                <Figure.Image alt="Technology Icon" src={item} />
              </Figure>
            ))}
          </InfiniteCarousel>

        </Container>
      </div>
    </div>
  );
};

export default Skills;
